
.card{
  background-color: #ffdc25;
  background-repeat: no-repeat;
  // @include filter-gradient(#53455b, #201e22, horizontal);
  // @include background-image(radial-gradient(circle at top,  rgba(83,69,91,1) 0,rgba(32,30,34,1) 70%));
  color: #fff;
  font-family: 'Lato', sans-serif;
  font-size: 100%;
  min-height: 100vh;
  line-height: 1.5;
  padding: 2.5em 0;

  position: absolute;
  top: 0px !important;
  width: 100%;
  z-index: -1;
  .card-container {
    margin: 0 auto !important;
    max-width: 1200px;
    position: relative;
    top: 120px;
  //   display: flex;
  // justify-content: center;
    
  }
  
  .grid-1-5 {
    border: 2px solid #5d4e65;
    min-height: 400px;
    padding: 1.25em;
    position: relative;
    text-align: center;
    transition: all .2s ease-in-out;
    background-color: #211f23;
    
    @media screen and (min-width: 700px) {
      & {
        float: left;
        width: 50%;
      }
      &:nth-child(odd) {
        clear: left;
      }
    }
    
    @media screen and (min-width: 800px) {
      & {      
        width: 33.3333333%;
      }
      &:nth-child(3n+1) {
        clear: left;
      }
      &:nth-child(odd) {
        clear: none;
      }    
    }
    
    @media screen and (min-width: 1120px) {
        
      
    }
    
  }
  .grid-1-5:hover {
    background-color: rgb(83,69,91);
    // @include filter-gradient(#53455b, #201d22, vertical);
    // @include background-image(linear-gradient(top,  rgba(83,69,91,1) 0%,rgba(32,29,34,1) 100%));  
    border-top: 2px solid #ec7a37;
    border-bottom: 2px solid #ff4f69;
    box-shadow: 0px 0px 10px 0px rgba(50, 50, 50, 1);
    transform: scale(1.025);
    z-index: 2;
  
    &:before, &:after {
      content: ""; 
      position: absolute; 
      background-color: rgb(246,125,53);
      // @include filter-gradient(#f67d35, #ff4f68, vertical);
      // @include background-image(linear-gradient(top,  rgba(246,125,53,1) 0%,rgba(255,79,104,1) 100%));
      top: -2px; 
      bottom: -2px; 
      width: 2px;    
    }
  
    &:before { 
      left: -2px; 
    }
    &:after { 
      right: -2px; 
    }  
  
    & .button {
      background-color: rgb(238,122,54);
      // @include filter-gradient(#ee7a36, #eb495d, horizontal);
      // @include background-image(linear-gradient(left,  rgba(238,122,54,1) 0%,rgba(235,73,93,1) 100%));    
    }
  }
  h2, h3, p, ul {
    margin: 0
  }
  h2 {
    font-size: 1em;
    font-weight: 400;
    margin: 0 0 0.5em;
  }
  h3 {
    font-size: 1.5em;
    letter-spacing: 0.0625em;
    margin: 0 0 0.3333333333333333em;
  }
  p {
    font-size: 0.875em;  
  }
  p, ul {
    margin:0 0 1.5em
  }
  ul {
    color: #796583;
    font-size: 0.75em;
    list-style-type: none;
    padding: 0;
  
    li {
      margin: 0 0 0.8333333333333333em;
    }
  }
  .button {
    background-color: #9c83aa;
    border-radius: 20px;
    color: #fff;
    font-size: 1em;
    font-weight: 700;
    padding: 0.75em 1.5em;
    position: absolute;
    bottom: 1.25em;
    left: 50%;
    margin-left: -60px;
    text-decoration: none;
    width: 120px;
  }
  .uppercase, .button, h2 {
    text-transform: uppercase;
  }
  sup, .small {
    font-size: 0.6125em;
  }
}


@media screen and (min-width: 1120px) {
    
  .card-container {
    display: flex;
  }
}
